<template>
  <div class="mine-task-page">
    <van-nav-bar
      class="task-nav-bar"
      :title="title"
      :border="false"
      left-arrow
      @click-left="onBack"
    />
    <div class="task-content">
      <van-pull-refresh
          v-model="refreshing"
          @refresh="onRefresh"
      >
        <van-list
          v-model="loading" 
          :error.sync="error"
          error-text="请求失败，点击重新加载"
          :finished="finished"
          finished-text="我是有底线的"
          @load="onLoad"
          v-show="!(contentLoaded && taskList.length == 0)"
        >

          <div class="task-contentCell " v-for="(item, index) in taskList" :key="index" @click="goDetail(item)" >
            <div class="cell-top" :class="(item.LINE_FLAG == 2 && item.status != 3 ? 'borderRed' : (item.LINE_FLAG == 1 && item.status != 3 ? 'borderYellow' : 'borderGrey' ))" >
              <div class="cell-communityName">
                <div>{{item.areaName}}{{item.buildName}}{{item.unitNo ? item.unitNo + "- " : ''}}{{item.roomName}}</div>
                <div class="status" v-if="item.relationFlag == 1 || item.relationFlag == 2">
                  {{item.relationFlag == 1 ? "主工单" : "子工单"}}
                </div>
              </div>

              <div class="fontRed work-status" v-if="item.status == '0'">待接单</div>
              <div class="fontGreen work-status" v-if="item.status == '1'">受理中</div>
              <div class="fontYellow work-status" v-if="item.status == '2' && (item.closeType == null || item.closeType == '')">待评价</div>
              <div class="fontBlack work-status" v-if="item.status == '3' && item.closeType == '0'">已结束</div>
              <div class="fontBlack work-status" v-if="item.status == '3' && item.closeType == '1'">暂时关闭</div>
              <div class="fontBlack work-status" v-if="item.status == '3' && item.closeType == '2'">已结束</div>
            </div>

            <div class="cell-content">
              <img :src="(item.picList && item.picList.length > 0) ? item.picList[0] : communityPic" alt="" v-on:error.once="showErrorImg($event)">
              <div class="cell-taskDetail">
                <div class="cell-taskTip">#{{item.labelName}}</div>
                <div class="cell-taskDes">{{item.content}}</div>
              </div>
            </div>

            <div class="cell-bottom">
              <div class="celll-confirmDate" v-if="!item.compLimitTime && item.confLimitTime">确认时限 {{item.confLimitTime}}</div>
              <div class="celll-confirmDate" v-else-if="item.compLimitTime && item.confLimitTime && item.status == '1'">处理时限 {{item.compLimitTime}}</div>
              <div class="celll-confirmDate" v-else-if="item.compLimitTime &&item.confLimitTime && item.status == '2'">完成时限 {{item.compLimitTime}}</div>
            </div>
          </div>
        </van-list>
        <div class="no-list" v-show="contentLoaded && taskList.length == 0">
          <img :src="noSourceImg" alt="">
          <div>暂无工单</div>
        </div>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>

  import {
    getMineWorkListService
  } from "../../services/mineService";
  import {store, storage} from "../../utils/store";

  import Vue from 'vue';
  import {Toast, List, PullRefresh } from 'vant';
  Vue.use(Toast).use(List).use(PullRefresh);

  // 完成logo
  import workFinishLogo from '@/assets/imgs/mine/mine_finishLogo.png'
  // 社区默认图片
  import communityPic from '@/assets/imgs/mine/mine_community.png'
  // 暂无工单图片
  import noSourceImg from '@/assets/imgs/workPlan/noSource.png'


  export default {
    name: "mineTask",
    created () {
      // this.getWorkList(true);
    },
    data() {
      return {
        active: 0,
        title: '我的工单',
        taskList: [],
        refreshing: false,
        loading: false,
        finished: false,
        error: false,
        // 当前页码值
        pageNum: 1,
        // 每页个数
        pageSize: 10,
        // 内容已加载
        contentLoaded: false,
        workFinishLogo: workFinishLogo,
        communityPic: communityPic,
        noSourceImg: noSourceImg,
      };
    },

    methods: {

      showErrorImg: function(event) {
        event.currentTarget.src = this.communityPic;
      },
      // 获取我的工单数据
      getWorkList(isRefresh) {
        let custNews = store.get('custNews');
        custNews = custNews ? custNews: storage.getStorage("custNews");
        let requestData = {
          userCode:custNews.USER_CODE,
          page:1,//分页页号
          rows:10,//每页条数
          order:"asc",
          sort:"name"
        };
        getMineWorkListService(requestData)
        .then(noteData => {
          this.loading = false;
          if(noteData.resultCode == 0) {
            if (isRefresh) {
              this.finished = false;
              this.refreshing = false;
              this.taskList = [];
            }

            noteData.data.list.forEach(element => {
              this.taskList.push(element);
            });
            // 加载完成
            if (this.pageNum * this.pageSize >= noteData.data.total) {
              this.finished = true;
            }
            this.pageNum++;
            this.contentLoaded = true;
          } else {
            this.loading = false;
            this.refreshing = false;
            this.error = true;
            Toast.fail(noteData.msg);
            this.contentLoaded = true;
          }
          
        })
        .catch(error => {
          this.loading = false;
          this.refreshing = false;
          this.error = true;
          Toast.fail(error);
          this.contentLoaded = true;
        });
      },
      // 加载更多
      onLoad() {
        this.error = false;
        this.loading = true;
        this.getWorkList(false);
      },
      //下拉刷新
      onRefresh() {
        this.error = false;
        this.loading = false;
        this.pageNum = 1;
        this.getWorkList(true);
      },
      // 跳转到工单详情页面
      goDetail: function(item) {
        store.set("workDetailComplCode", item.complCode);
        store.set("workDetailShowBtn", 1);
        this.$router.push({
          name: "workDetail"
        });
      },
      onBack() {
        history.back();
      },
    }
  };
</script>

